.grid-container {
  display: grid;
  grid-template-columns: auto auto auto;
  background-color: #3995dc;
  padding: 15px;
}

input[type="text"],
select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
input[type="number"],
select {
  width: 10%;
  padding: 12px 20px;
  margin: 8px 0;
  display: block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 25px;
}

div {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}

.btn {
  background-color: #e46b0e;
  border: none;
  color: white;
  padding: 25px 72px;
  text-align: center;
  text-decoration: none;
  display: block;
  font-size: 26px;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  opacity: 0.4;
  transition: 0.5s;
}
.btn:hover {
  opacity: 1;
  background-color: #4caf50;
  font-weight: bold;
}
